<template>
  <a-modal v-model:visible="visible" :footer="false" @cancel="closeModal">
    <template #title>
      {{ title }}
    </template>
    <h4 style="margin-top: 0"></h4>
    <a-typography-paragraph copyable>{{ link }}</a-typography-paragraph>
    <h4></h4>
    <img :src="codeImg" />
  </a-modal>
</template>

<script setup lang="ts">
import { defineExpose, defineProps, ref, withDefaults } from "vue";
// @ts-ignore
import QRCode from "qrcode";
import message from "@arco-design/web-vue/es/message";

/**
 * 
 */
interface Props {
  // 
  link: string;
  // 
  title: string;
}

/**
 * 
 */
const props = withDefaults(defineProps<Props>(), {
  link: () => "https://laoyujianli.com/share/yupi",
  title: () => "",
});

// 
const codeImg = ref();

// 
const visible = ref(false);

// 
const openModal = () => {
  visible.value = true;
};

// 
defineExpose({
  openModal,
});

// 
const closeModal = () => {
  visible.value = false;
};

// 
QRCode.toDataURL(props.link)
  .then((url: any) => {
    codeImg.value = url;
  })
  .catch((err: any) => {
    console.error(err);
    message.error("" + err.message);
  });
</script>

<style scoped></style>
