<template>
  <div id="addScoringResultPage">
    <h2 style="margin-bottom: 32px"></h2>
    <a-form
      :model="form"
      :style="{ width: '480px' }"
      label-align="left"
      auto-label-width
      @submit="handleSubmit"
    >
      <a-form-item label=" id">
        {{ appId }}
      </a-form-item>
      <a-form-item v-if="updateId" label=" id">
        {{ updateId }}
      </a-form-item>
      <a-form-item field="resultName" label="">
        <a-input v-model="form.resultName" placeholder="" />
      </a-form-item>
      <a-form-item field="resultDesc" label="">
        <a-input v-model="form.resultDesc" placeholder="" />
      </a-form-item>
      <a-form-item field="resultPicture" label="">
        <a-input
          v-model="form.resultPicture"
          placeholder=""
        />
      </a-form-item>
      <a-form-item field="resultProp" label="">
        <a-input-tag
          v-model="form.resultProp"
          :style="{ width: '320px' }"
          placeholder=""
          allow-clear
        />
      </a-form-item>
      <a-form-item field="resultScoreRange" label="">
        <a-input-number
          v-model="form.resultScoreRange"
          placeholder=""
        />
      </a-form-item>
      <a-form-item>
        <a-button type="primary" html-type="submit" style="width: 120px">
          
        </a-button>
      </a-form-item>
    </a-form>
    <!--  -->
    <h2 style="margin-bottom: 32px"></h2>
    <ScoringResultTable :appId="appId" :doUpdate="doUpdate" ref="tableRef" />
  </div>
</template>

<script setup lang="ts">
import { defineProps, ref, withDefaults } from "vue";
import API from "@/api";
import { useRouter } from "vue-router";
import {
  addScoringResultUsingPost,
  editScoringResultUsingPost,
} from "@/api/scoringResultController";
import message from "@arco-design/web-vue/es/message";
import ScoringResultTable from "@/add/components/ScoringResultTable.vue";

interface Props {
  appId: string;
}

const props = withDefaults(defineProps<Props>(), {
  appId: () => {
    return "";
  },
});

const router = useRouter();
const tableRef = ref();

// 
const form = ref({
  resultDesc: "",
  resultName: "",
  resultPicture: "",
} as API.ScoringResultAddRequest);

const updateId = ref<any>();

const doUpdate = (scoringResult: API.ScoringResultVO) => {
  updateId.value = scoringResult.id;
  form.value = scoringResult;
};

/**
 * 
 */
const handleSubmit = async () => {
  if (!props.appId) {
    return;
  }
  let res: any;
  // 
  if (updateId.value) {
    res = await editScoringResultUsingPost({
      id: updateId.value as any,
      ...form.value,
    });
  } else {
    // 
    res = await addScoringResultUsingPost({
      appId: props.appId as any,
      ...form.value,
    });
  }
  if (res.data.code === 0) {
    message.success("");
  } else {
    message.error("" + res.data.message);
  }
  if (tableRef.value) {
    tableRef.value.loadData();
    updateId.value = undefined;
  }
};
</script>
