<!-- eslint-disable vue/valid-v-for -->
<template>
  <div id="addAppPage">
    <h2 style="margin-bottom: 32px"></h2>
    <a-form
      :model="form"
      :style="{ width: '480px' }"
      label-align="left"
      auto-label-width
      @submit="handleSubmit"
    >
      <a-form-item
        field="appName"
        tooltip=","
        label=""
      >
        <a-input v-model="form.appName" placeholder="" />
      </a-form-item>
      <a-form-item field="appDesc" label="">
        <a-input v-model="form.appDesc" placeholder="" />
      </a-form-item>
      <a-form-item field="appIcon" label="">
        <a-input v-model="form.appIcon" placeholder="" />
      </a-form-item>
      <a-form-item field="appType" label="">
        <a-select
          v-model="form.appType"
          :style="{ width: '320px' }"
          placeholder=""
        >
          <a-option
            v-for="(value, key) of APP_TYPE_MAP"
            :value="Number(key)"
            :label="value"
          />
        </a-select>
      </a-form-item>
      <a-form-item field="scoringStrategy" label="">
        <a-select
          v-model="form.scoringStrategy"
          :style="{ width: '320px' }"
          placeholder=""
        >
          <a-option
            v-for="(value, key) of APP_SCORING_STRATEGY_MAP"
            :value="Number(key)"
            :label="value"
          />
        </a-select>
      </a-form-item>
      <a-form-item>
        <a-button type="primary" html-type="submit" style="width: 120px">
          
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script setup lang="ts">
import { defineProps, ref, watchEffect, withDefaults } from "vue";
import API from "@/api";
import message from "@arco-design/web-vue/es/message";
import { useRouter } from "vue-router";
import {
  addAppUsingPost,
  editAppUsingPost,
  getAppVoByIdUsingGet,
} from "@/api/appController";
import { APP_SCORING_STRATEGY_MAP, APP_TYPE_MAP } from "@/constant/app";

interface Props {
  id: string;
}

const props = withDefaults(defineProps<Props>(), {
  id: () => {
    return "";
  },
});

const router = useRouter();

const form = ref({
  appDesc: "",
  appIcon: "",
  appName: "",
  appType: 0,
  scoringStrategy: 0,
} as API.AppAddRequest);

const oldApp = ref<API.AppVO>();

/**
 * 
 */
const loadData = async () => {
  if (!props.id) {
    return;
  }
  const res = await getAppVoByIdUsingGet({
    id: props.id as any,
  });
  if (res.data.code === 0 && res.data.data) {
    oldApp.value = res.data.data;
    form.value = res.data.data;
  } else {
    message.error("" + res.data.message);
  }
};

// 
watchEffect(() => {
  loadData();
});

/**
 * 
 */
const handleSubmit = async () => {
  let res: any;
  // 
  if (props.id) {
    res = await editAppUsingPost({
      id: props.id as any,
      ...form.value,
    });
  } else {
    // 
    res = await addAppUsingPost(form.value);
  }
  if (res.data.code === 0) {
    message.success("");
    setTimeout(() => {
      router.push(`/app/detail/${props.id || res.data.data}`);
    }, 1100);
  } else {
    message.error("" + res.data.message);
  }
};
</script>
