<template>
  <a-form
    :model="formSearchParams"
    :style="{ marginBottom: '20px' }"
    layout="inline"
    @submit="doSearch"
  >
    <a-form-item field="userName" label="">
      <a-input
        allow-clear
        v-model="formSearchParams.userName"
        placeholder=""
      />
    </a-form-item>
    <a-form-item field="userProfile" label="">
      <a-input
        allow-clear
        v-model="formSearchParams.userProfile"
        placeholder=""
      />
    </a-form-item>
    <a-form-item>
      <a-button type="primary" html-type="submit" style="width: 100px">
        
      </a-button>
    </a-form-item>
  </a-form>
  <a-table
    :scroll="scroll"
    :columns="columns"
    :data="dataList"
    :pagination="{
      showTotal: true,
      pageSize: searchParams.pageSize,
      current: searchParams.current,
      total,
    }"
    @page-change="onPageChange"
  >
    <template #userAvatar="{ record }">
      <a-image width="67" :src="record.userAvatar" />
    </template>
    <template #userRole="{ record }">
      <a-space>
        <a-tag :color="record.userRole === 'admin' ? 'red' : 'green'">
          {{ record.userRole === "admin" ? "" : "" }}
        </a-tag>
      </a-space>
    </template>
    <template #createTime="{ record }">
      {{ dayjs(record.createTime).format("YYYY-MM-DD HH:mm:ss") }}
    </template>
    <template #updateTime="{ record }">
      {{ dayjs(record.updateTime).format("YYYY-MM-DD HH:mm:ss") }}
    </template>
    <template #optional="{ record }">
      <a-space>
        <a-popconfirm
          content="?"
          @ok="doDelete(record)"
          type="warning"
        >
          <a-button status="danger"></a-button>
        </a-popconfirm>
      </a-space>
    </template>
  </a-table>
</template>

<script setup lang="ts">
import { ref, watchEffect } from "vue";
import {
  deleteUserUsingPost,
  listUserByPageUsingPost,
} from "@/api/userController";
import API from "@/api";
import message from "@arco-design/web-vue/es/message";
import { dayjs } from "@arco-design/web-vue/es/_utils/date";
import { useLoginUserStore } from "../../store/userStore";

const scroll = {
  x: 1800,
  y: 1500,
};
const formSearchParams = ref<API.UserQueryRequest>({});

// 
const initSearchParams = {
  current: 1,
  pageSize: 10,
};
//  initSearchParams  searchParams 
const searchParams = ref<API.UserQueryRequest>({
  ...initSearchParams,
});
const dataList = ref<API.User[]>([]);
const total = ref<number>(0);

/**
 * 
 */
const loadData = async () => {
  const res = await listUserByPageUsingPost(searchParams.value);
  if (res.data.code === 0) {
    dataList.value = res.data.data?.records || [];
    total.value = res.data.data?.total || 0;
  } else {
    message.error("" + res.data.message);
  }
};

/**
 * 
 */
const doSearch = () => {
  searchParams.value = {
    ...initSearchParams,
    ...formSearchParams.value,
  };
};

/**
 * 
 * @param page
 */
const onPageChange = (page: number) => {
  searchParams.value = {
    ...searchParams.value,
    current: page,
  };
};

/**
 * 
 * @param record
 */
const doDelete = async (record: API.User) => {
  if (!record.id) {
    return;
  }

  const res = await deleteUserUsingPost({
    id: record.id,
  });
  if (res.data.code === 0) {
    loadData();
  } else {
    message.error("" + res.data.message);
  }
};

/**
 *  searchParams 
 */
watchEffect(() => {
  loadData();
});

// 
const columns = [
  {
    title: "id",
    ellipsis: true,
    tooltip: true,
    width: 150,
    dataIndex: "id",
  },
  {
    title: "",
    dataIndex: "userAccount",
  },
  {
    title: "",
    dataIndex: "userName",
  },
  {
    title: "",
    dataIndex: "userAvatar",
    slotName: "userAvatar",
  },
  {
    title: "",
    dataIndex: "userProfile",
  },
  {
    title: "",
    dataIndex: "userRole",
    slotName: "userRole",
  },
  {
    title: "",
    dataIndex: "createTime",
    slotName: "createTime",
  },
  {
    title: "",
    dataIndex: "updateTime",
    slotName: "updateTime",
  },
  {
    title: "",
    fixed: "right",
    width: 150,
    slotName: "optional",
  },
];
</script>
