<template>
  <a-form
    :model="formSearchParams"
    :style="{ marginBottom: '20px' }"
    layout="inline"
    @submit="doSearch"
  >
    <a-form-item field="resultName" label="">
      <a-input
        v-model="formSearchParams.resultName"
        placeholder=""
        allow-clear
      />
    </a-form-item>
    <a-form-item field="resultDesc" label="">
      <a-input
        v-model="formSearchParams.resultDesc"
        placeholder=""
        allow-clear
      />
    </a-form-item>
    <a-form-item field="appId" label=" id">
      <a-input
        v-model="formSearchParams.appId"
        placeholder=" id"
        allow-clear
      />
    </a-form-item>
    <a-form-item>
      <a-button type="primary" html-type="submit" style="width: 100px">
        
      </a-button>
    </a-form-item>
  </a-form>
  <a-table
    :scroll="scroll"
    :columns="columns"
    :data="dataList"
    :pagination="{
      showTotal: true,
      pageSize: searchParams.pageSize,
      current: searchParams.current,
      total,
    }"
    @page-change="onPageChange"
  >
    <template #resultPicture="{ record }">
      <a-image width="64" :src="record.resultPicture" />
    </template>
    <!---------------------------------------------------->
    <template #scoringStrategy="{ record }">
      <a-space>
        <a-tag
          bordered
          :color="record.scoringStrategy === 0 ? 'green' : 'magenta'"
          >{{ APP_SCORING_STRATEGY_MAP[record.scoringStrategy] }}
        </a-tag>
      </a-space>
    </template>
    <template #createTime="{ record }">
      {{ dayjs(record.createTime).format("YYYY-MM-DD HH:mm:ss") }}
    </template>
    <template #updateTime="{ record }">
      {{ dayjs(record.updateTime).format("YYYY-MM-DD HH:mm:ss") }}
    </template>
    <template #appType="{ record }">
      <a-space>
        <a-button :status="record.appType === 0 ? 'warning' : 'success'"
          >{{ APP_TYPE_MAP[record.appType] }}
        </a-button>
      </a-space>
    </template>
    <template #optional="{ record }">
      <a-space>
        <a-popconfirm
          content="?"
          @ok="doDelete(record)"
          type="warning"
        >
          <a-button status="danger"></a-button>
        </a-popconfirm>
      </a-space>
    </template>
  </a-table>
</template>

<script setup lang="ts">
import { ref, watchEffect } from "vue";
import {
  deleteUserAnswerUsingPost,
  listMyUserAnswerVoByPageUsingPost,
} from "@/api/userAnswerController";
import API from "@/api";
import message from "@arco-design/web-vue/es/message";
import { dayjs } from "@arco-design/web-vue/es/_utils/date";
import { APP_SCORING_STRATEGY_MAP, APP_TYPE_MAP } from "@/constant/app";

const scroll = {
  x: 1700,
  y: 1200,
};
const formSearchParams = ref<API.UserAnswerQueryRequest>({});

// 
const initSearchParams = {
  current: 1,
  pageSize: 10,
  sortField: "createTime",
  sortOrder: "descend",
};

const searchParams = ref<API.UserAnswerQueryRequest>({
  ...initSearchParams,
});
const dataList = ref<API.UserAnswerVO[]>([]);
const total = ref<number>(0);
/**
 * 
 */
const loadData = async () => {
  const res = await listMyUserAnswerVoByPageUsingPost(searchParams.value);
  if (res.data.code === 0) {
    dataList.value = res.data.data?.records || [];
    total.value = res.data.data?.total || 0;
  } else {
    message.error("" + res.data.message);
  }
};

/**
 * 
 */
const doSearch = () => {
  searchParams.value = {
    ...initSearchParams,
    ...formSearchParams.value,
  };
};

/**
 * 
 * @param page
 */
const onPageChange = (page: number) => {
  searchParams.value = {
    ...searchParams.value,
    current: page,
  };
};

/**
 * 
 * @param record
 */
const doDelete = async (record: API.UserAnswer) => {
  if (!record.id) {
    return;
  }

  const res = await deleteUserAnswerUsingPost({
    id: record.id,
  });
  if (res.data.code === 0) {
    loadData();
  } else {
    message.error("" + res.data.message);
  }
};

/**
 *  searchParams 
 */
watchEffect(() => {
  loadData();
});

// 
const columns = [
  {
    title: "id",
    ellipsis: true,
    tooltip: true,
    dataIndex: "id",
  },
  {
    title: "",
    width: 180,
    ellipsis: true,
    tooltip: true,
    dataIndex: "choices",
  },
  {
    title: "",
    ellipsis: true,
    tooltip: true,
    dataIndex: "resultId",
  },
  {
    title: "",
    width: 150,
    dataIndex: "resultName",
  },
  {
    title: "",
    width: 250,
    ellipsis: true,
    tooltip: true,
    dataIndex: "resultDesc",
  },
  {
    title: "",
    dataIndex: "resultPicture",
    slotName: "resultPicture",
  },
  {
    title: "",
    dataIndex: "resultScore",
  },
  {
    title: " id",
    ellipsis: true,
    tooltip: true,
    dataIndex: "appId",
  },
  {
    title: "",
    dataIndex: "scoringStrategy",
    slotName: "scoringStrategy",
  },
  {
    title: "",
    width: 170,
    dataIndex: "createTime",
    slotName: "createTime",
  },
  {
    title: "",
    fixed: "right",
    width: 105,
    dataIndex: "appType",
    slotName: "appType",
  },
  {
    title: "",
    fixed: "right",
    width: 110,
    slotName: "optional",
  },
];
</script>
