<template>
  <div id="appDetailPage">
    <a-card class="card">
      <a-row style="margin-bottom: 16px">
        <a-col flex="auto" class="content-wrapper">
          <h1>{{ data.appName }}</h1>
          <p>{{ data.appDesc }}</p>
          <p>{{ APP_TYPE_MAP[data.appType] }}</p>
          <div tooltip="Please enter username">
            <p>ID{{ data.id }}</p>
          </div>
          <p>{{ APP_SCORING_STRATEGY_MAP[data.scoringStrategy] }}</p>
          <p>
            <a-space>
              
              <div :style="{ display: 'flex', alignItems: 'center' }">
                <a-avatar
                  :size="24"
                  :image-url="data.user?.userAvatar"
                  :style="{ marginRight: '8px' }"
                />
                <a-typography-text
                  >{{ data.user?.userName ?? "" }}
                </a-typography-text>
              </div>
            </a-space>
          </p>
          <p>
            {{ dayjs(data.createTime).format("YYYY-MM-DD HH:mm:ss") }}
          </p>
          <a-space size="medium">
            <a-button type="primary" :href="`/answer/do/${id}`"
              >
            </a-button>
            <a-button @click="doShare"></a-button>
            <a-button v-if="isMy" :href="`/add/question/${id}`"
              >
            </a-button>
            <a-button v-if="isMy" :href="`/add/scoring_result/${id}`"
              >
            </a-button>
            <a-button v-if="isMy" :href="`/add/app/${id}`"></a-button>
          </a-space>
        </a-col>
        <a-col flex="330px">
          <a-image width="400px" height="250px" :src="data.appIcon" />
        </a-col>
      </a-row>
    </a-card>
    <ShareModal :link="shareLink" title="" ref="shareModalRef" />
  </div>
</template>

<script setup lang="ts">
import { computed, defineProps, ref, watchEffect, withDefaults } from "vue";
import API from "@/api";
import { getAppVoByIdUsingGet } from "@/api/appController";
import message from "@arco-design/web-vue/es/message";
import { useRouter } from "vue-router";
import { dayjs } from "@arco-design/web-vue/es/_utils/date";
import { useLoginUserStore } from "@/store/userStore";
import { APP_SCORING_STRATEGY_MAP, APP_TYPE_MAP } from "@/constant/app";
import ShareModal from "@/add/components/ShareModal.vue";

interface Props {
  id: string;
}

const props = withDefaults(defineProps<Props>(), {
  id: () => {
    return "";
  },
});

const router = useRouter();

const data = ref<API.AppVO>({});

// 
const loginUserStore = useLoginUserStore();
let loginUserId = loginUserStore.loginUser?.id;

//  computed
const isMy = computed(() => {
  return loginUserId && loginUserId === data.value.userId;
});

/**
 * 
 */
const loadData = async () => {
  if (!props.id) {
    return;
  }
  const res = await getAppVoByIdUsingGet({
    id: props.id as any,
  });
  if (res.data.code === 0) {
    data.value = res.data.data as any;
  } else {
    message.error("" + res.data.message);
  }
};

/**
 *  searchParams 
 */
watchEffect(() => {
  loadData();
});

// 
const shareModalRef = ref();

// 
const shareLink = `${window.location.protocol}//${window.location.host}/app/detail/${props.id}`;

// 
const doShare = (e: Event) => {
  if (shareModalRef.value) {
    shareModalRef.value.openModal();
  }
  // 
  e.stopPropagation();
};
</script>

<style scoped>
#appDetailPage {
}

.card {
  width: 1150px;
  margin: 0 auto;
  background: linear-gradient(135deg, #ffffff, #ffffff, #ffffff);
  border-radius: 9px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1); /*  */
}

#appDetailPage .content-wrapper > * {
  margin-bottom: 24px;
}
</style>
