<template>
  <div id="addQuestionPage">
    <h2 style="margin-bottom: 32px"></h2>
    <a-back-top />
    <a-form
      :model="questionContent"
      :style="{ width: '480px' }"
      label-align="left"
      auto-label-width
      @submit="handleSubmit"
    >
      <a-form-item label=" id">
        {{ appId }}
      </a-form-item>
      <a-form-item label="" :content-flex="false" :merge-props="false">
        <a-space size="medium">
          <a-button @click="addQuestion(questionContent.length)">
            
          </a-button>
          <!-- AI  -->
          <AiGenerateQuestionDrawer
            :appId="appId"
            :onSuccess="onAiGenerateSuccess"
            :onSSESuccess="onAiGenerateSuccessSSE"
            :onSSEClose="onSSEClose"
            :onSSEStart="onSSEStart"
          />
        </a-space>
        <!--  -->
        <div v-for="(question, index) in questionContent" :key="index">
          <a-space size="large">
            <h3> {{ index + 1 }}</h3>
            <a-button size="small" @click="addQuestion(index + 1)">
              
            </a-button>
            <a-button
              size="small"
              status="danger"
              @click="deleteQuestion(index)"
            >
              
            </a-button>
          </a-space>
          <a-form-item field="posts.post1" :label="` ${index + 1} `">
            <a-input v-model="question.title" placeholder="" />
          </a-form-item>
          <!--   -->
          <a-space size="large">
            <h4> {{ index + 1 }} </h4>
            <a-button
              size="small"
              @click="addQuestionOption(question, question.options.length)"
            >
              
            </a-button>
          </a-space>
          <a-form-item
            v-for="(option, optionIndex) in question.options"
            :key="optionIndex"
            :label="` ${optionIndex + 1}`"
            :content-flex="false"
            :merge-props="false"
          >
            <a-form-item label=" key">
              <a-input v-model="option.key" placeholder=" key" />
            </a-form-item>
            <a-form-item label="">
              <a-input v-model="option.value" placeholder="" />
            </a-form-item>
            <a-form-item label="">
              <a-input v-model="option.result" placeholder="" />
            </a-form-item>
            <a-form-item label="">
              <a-input-number
                v-model="option.score"
                placeholder=""
              />
            </a-form-item>
            <a-space size="large">
              <a-button
                size="mini"
                @click="addQuestionOption(question, optionIndex + 1)"
              >
                
              </a-button>
              <a-button
                size="mini"
                status="danger"
                @click="deleteQuestionOption(question, optionIndex as any)"
              >
                
              </a-button>
            </a-space>
          </a-form-item>
          <!--  -->
        </div>
      </a-form-item>
      <a-form-item>
        <a-button type="primary" html-type="submit" style="width: 120px">
          
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script setup lang="ts">
import { defineProps, ref, watchEffect, withDefaults } from "vue";
import API from "@/api";
import { useRouter } from "vue-router";
import {
  addQuestionUsingPost,
  editQuestionUsingPost,
  listQuestionVoByPageUsingPost,
} from "@/api/questionController";
import message from "@arco-design/web-vue/es/message";
import AiGenerateQuestionDrawer from "@/add/components/AiGenerateQuestionDrawer.vue";

interface Props {
  appId: string;
}

const props = withDefaults(defineProps<Props>(), {
  appId: () => {
    return "";
  },
});
const router = useRouter();
// reactive
// 
// const oldApp = ref<API.AppVO>();
// 
const questionContent = ref<API.QuestionContentDTO[]>([]);
/**
 * 
 * @param index
 */
const addQuestion = (index: number) => {
  questionContent.value.splice(index, 0, {
    title: "",
    options: [],
  });
};
/**
 * 
 * @param index
 */
const deleteQuestion = (index: number) => {
  questionContent.value.splice(index, 1);
};
/**
 * 
 * @param question
 * @param index
 */
const addQuestionOption = (question: API.QuestionContentDTO, index: number) => {
  if (!question.options) {
    question.options = [];
  }
  question.options.splice(index, 0, {
    key: "",
    value: "",
  });
};
/**
 * 
 * @param question
 * @param index
 */
const deleteQuestionOption = (
  question: API.QuestionContentDTO,
  index: number
) => {
  if (!question.options) {
    question.options = [];
  }
  question.options.splice(index, 1);
};
const oldQuestion = ref<API.QuestionVO>();
/**
 * 
 */
const loadData = async () => {
  if (!props.appId) {
    return;
  }
  const res = await listQuestionVoByPageUsingPost({
    appId: props.appId as any,
    current: 1,
    pageSize: 1,
    sortField: "createTime",
    sortOrder: "descend",
  });
  if (res.data.code === 0 && res.data.data?.records) {
    oldQuestion.value = res.data.data?.records[0];
    if (oldQuestion.value) {
      questionContent.value = oldQuestion.value.questionContent ?? [];
    }
  } else {
    message.error("" + res.data.message);
  }
};

// 
watchEffect(() => {
  loadData();
});

/**
 * 
 */
const handleSubmit = async () => {
  if (!props.appId || !questionContent.value) {
    return;
  }
  let res: any;
  // 
  if (oldQuestion.value?.id) {
    res = await editQuestionUsingPost({
      id: oldQuestion.value.id,
      questionContent: questionContent.value,
    });
  } else {
    // 
    res = await addQuestionUsingPost({
      appId: props.appId as any,
      questionContent: questionContent.value,
    });
  }
  if (res.data.code === 0) {
    message.success("");
    setTimeout(() => {
      router.push(`/app/detail/${props.appId}`);
    }, 3000);
  } else {
    message.error("" + res.data.message);
  }
};

/**
 * AI 
 */
const onAiGenerateSuccess = (result: API.QuestionContentDTO[]) => {
  message.success(`AI  ${result.length} `);
  questionContent.value = [...questionContent.value, ...result];
};

/**
 * AI SSE
 */
const onAiGenerateSuccessSSE = (result: API.QuestionContentDTO) => {
  questionContent.value = [...questionContent.value, result];
};

/**
 * SSE 
 * @param event
 */
const onSSEStart = (event: any) => {
  message.success("");
};

/**
 * SSE 
 * @param event
 */
const onSSEClose = (event: any) => {
  message.success("");
};
</script>
<style>
#addQuestionPage {
}
</style>
